<template>
    <el-container>
      <!-- 顶栏标题 -->
      <el-header style="font-size: 40px;background-color: rgb(177, 199, 236)">学生宿舍管理系统</el-header>
      <el-container>
        <!-- 侧边菜单 -->
        <el-aside width="200px" style="background-color: #545c64; height:calc(100vh - 60px)">
          <!-- <el-menu :default-openeds="['1', '3']" style="background-color: rgb(238, 241, 246)">
            <el-menu-item index="1-1">
              <router-link to="/stu" style="text-decoration: none; color: inherit;">
                <i class="el-icon-user">学生个人信息</i>
              </router-link>
            </el-menu-item>
            <el-menu-item index="1-2">
              <router-link to="/room" style="text-decoration: none; color: inherit;">
                <i class="el-icon-office-building">宿舍规格信息</i>
              </router-link>
            </el-menu-item>
            <el-menu-item index="1-3">
            </el-menu-item>
          </el-menu> -->
              <el-menu
                default-active="2"
                class="el-menu-vertical-demo"
                @open="handleOpen()"
                @close="handleClose()"
                background-color="#545c64"
                text-color="#fff"
                active-text-color="#ffd04b">
                <el-menu-item index="1-1"><!--学生个人信息-->
                  <router-link to="/stu" style="text-decoration: none; color: inherit;">
                    <i class="el-icon-user"></i>
                    <span>学生个人信息</span>
                  </router-link>
                </el-menu-item>
                <el-menu-item index="1-2"><!--宿舍规格信息-->
                  <router-link to="/room" style="text-decoration: none; color: inherit;">
                    <i class="el-icon-office-building"></i>
                    <span>宿舍规格信息</span>
                  </router-link>
                </el-menu-item>

                <!-- 多级菜单示例 -->
                <!-- <el-submenu index="1-3">
                  <template slot="title">
                    <i class="el-icon-location"></i>
                    <span>导航一</span>
                  </template>
                  <el-menu-item-group>
                    <template slot="title">分组一</template>
                    <el-menu-item index="1-1">选项1</el-menu-item>
                    <el-menu-item index="1-2">选项2</el-menu-item>
                  </el-menu-item-group>
                  <el-menu-item-group title="分组2">
                    <el-menu-item index="1-3">选项3</el-menu-item>
                  </el-menu-item-group>
                  <el-submenu index="1-4">
                    <template slot="title">选项4</template>
                    <el-menu-item index="1-4-1">选项1</el-menu-item>
                  </el-submenu>
                </el-submenu> -->
              </el-menu>


        </el-aside>
        <el-main><router-view></router-view></el-main>
      </el-container>
    </el-container>
</template>
  
  <style scoped>
  </style>
  
  <script>
  //import axios from 'axios';
  import instance from '../request.js';
  
  // /*封装get方法*/
  // export function get(url, params = {}) {
  //   return new Promise((resolve, reject) => {
  //     axios.get(url, { params: params }).then(response => {
  //       resolve(response.data);
  //     })
  //       .catch(err => {
  //         reject(err);
  //       });
  //   });
  // }
  
  // /*封装post方法*/
  // export function post(url, data = {}) {
  //   return new Promise((resolve, reject) => {
  //     axios.post(url, data).then(response => {
  //       resolve(response.data);
  //     })
  //       .catch(err => {
  //         reject(err);
  //       });
  //   });
  // }
  
  export default {
    data() {
      return {
        //roomData: [],
        stuData: [],
        tempData: [],
        chooseData: [],
  
        form: {
          unit: '',
          roomid: '',
          id: '',
          name: '',
          gender: '男',
          age: '',
          department: '计算机',
          grade: '1',
          phone: '',
          roommsg: ''
        },
  
        editform: {
          unit: '',
          roomid: '',
          id: '',
          name: '',
          gender: '男',
          age: '',
          department: '计算机',
          grade: '1',
          phone: '',
          roommsg: ''
        },
  
        searchForm: {
          unit: '',
          roomid: '',
          name: '',
          gender: '',
          department: '',
          grade: '',
          roommsg: ''
        },
  
        dialogFormVisible: false,
        EditFormVisible: false,
        formLabelWidth: '120px',
      }
    },
  
  
    methods: {
      handleOpen(key, keyPath) {
        console.log(key, keyPath);
      },
      handleClose(key, keyPath) {
        console.log(key, keyPath);
      },
  
      /*新增数据表单提交*/
      form_Submit() {
        if (this.form.name == "" || this.form.gender == "" ||
          this.form.age == "" || this.form.department == "" ||
          this.form.grade == "" || this.form.phone == "" ||
          this.form.name == null || this.form.gender == null ||
          this.form.age == null || this.form.department == null ||
          this.form.grade == null || this.form.phone == null) {
          this.$message.error('提交失败！个人信息不可留空！');
        }
        else if ((this.form.unit == "" || this.form.unit == "null") ^ (this.form.roomid == "" || this.form.roomid == "null")) {
          this.$message.error('提交失败！楼栋和宿舍不可二填一！');
        }
        else {
          instance.post("/stu", this.form).then((res) => {
            if (res.data.msg == "countmax") {
              this.$message.error('提交失败！该宿舍已满人！请更换宿舍！');
            } else if(res.data.msg == "id_repeat") {
              this.$message.error('提交失败！该学号已存在！请更换学号！');
            } 
            else {
              this.$message({ message: '提交成功！请刷新网页查看最新数据', type: 'success' });
              console.log(res.data);
            }
          }).catch(error => {
            this.$message.error('提交失败！该宿舍不存在！请更换宿舍！');
            console.error(error);
          });
          //alert(JSON.stringify(this.form));
        }
      },
  
      /*搜索表单提交*/
      search_Submit() {
        //alert(JSON.stringify(this.searchForm))
        instance.post("/stu/search", this.searchForm).then((res) => {
          console.log(res.data.data);
          this.stuData = res.data.data;
          this.$message({ message: '查询成功！共' + res.data.data.length + '条数据', type: 'success' });
        }).catch(error => {
          console.error(error);
        });
      },
  
      /*提交修改*/
      edit_Submit() {
        if (this.editform.name == "" || this.editform.gender == "" ||
          this.editform.age == "" || this.editform.department == "" ||
          this.editform.grade == "" || this.editform.phone == "" ||
          this.editform.name == null || this.editform.gender == null ||
          this.editform.age == null || this.editform.department == null ||
          this.editform.grade == null || this.editform.phone == null) {
          this.$message.error('提交失败！信息不可留空！');
        }
        else if ((this.editform.unit == "" || this.editform.unit == "null") ^ (this.editform.roomid == "" || this.editform.roomid == "null")) {
          this.$message.error('提交失败！楼栋和宿舍不可二填一！');
        }
        else {
          instance.post("/stu/update", this.editform).then((res) => {
            if (res.data.msg == "count max") {
              this.$message.error('提交失败！该宿舍已满人！请更换宿舍！');
            } else {
              this.$message({ message: '提交成功！请刷新网页查看最新数据', type: 'success' });
              console.log(res.data);
            }
          }).catch(error => {
            this.$message.error('提交失败！该宿舍不存在！请更换宿舍！');
            console.error(error);
          });
        }
      },
  
      /*批量删除传入ids数组的学生*/
      delete_ids(ids) {
        this.$confirm('此操作将永久删除指定学生所有信息, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          instance.delete("/stu/ids/" + ids).then((res) => {
            console.log(res.data);
            this.$message({type: 'success',message: '删除成功!请刷新网页查看最新数据'});
          }).catch(error => {
            console.error(error);
          });
  
        }).catch(() => {
          this.$message({type: 'info',message: '已取消删除'});
        });
        //alert(JSON.stringify(data));
      },
  
      /*年级/院系优先分配宿舍*/
      assign(method) {
        this.$confirm('此操作将按照指定年级/院系升序,宿舍楼号升序自动分配宿舍\r\n是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          instance.get("/stu/assign/"+method).then((res) => {
            console.log(res.data);
            this.$message({ message: '自动分配宿舍成功！请刷新界面查看最新数据', type: 'success' });
          }).catch(error => {
            console.error(error);
          });
  
        }).catch(() => {
          this.$message({type: 'info',message: '已取消分配'});
        });
      },
  
      /*重置表单*/
      resetForm(formName) {
        this.$refs[formName].resetFields();
      },
  
      /*批量删除勾选内容 */
      delete_choose() {
        var ids=new Array();
        for (var i = 0; i < this.chooseData.length; i++)
          ids[i]=this.chooseData[i].id;
        this.delete_ids(ids);
      },
  
      /*取消勾选*/ 
      cancel_choose(rows) {//带参则反选传入参数的行
        if (rows) 
          rows.forEach(row => {this.$refs.multiple_Table.toggleRowSelection(row);});
        else 
          this.$refs.multiple_Table.clearSelection();
      },
  
      /*当选择项发生变化时触发该事件*/
      handleSelectionChange(choose_data) {
        this.chooseData = choose_data;
        //alert(JSON.stringify(choose_data));
      },
  
      /*清除token退出登录*/
      quit() {
        localStorage.clear();
        this.$router.push("/login");
        this.$message.success('已退出登录');
        //alert(JSON.stringify(choose_data));
      },
    },
  
    /*生命周期函数*/
    mounted() {
      instance.post("/stu/search", this.searchForm).then((res) => {
        console.log(res.data.msg);
        if (res.data.msg=="NOT_LOGIN") {
          this.$router.push("/login");
          this.$message.error('登录信息过期，请重新登录');
        }else{
        console.log(res.data.data);
        this.stuData = res.data.data;
        }
      }).catch(error => {
        console.error(error);
      });
    }
  };
  </script>